@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');

*,
*::before,
*::after {
    box-sizing: border-box;
    border-radius: 2.5px;
}

body {
    margin: 0;
    background: linear-gradient(to right, rgb(187, 187, 187), rgb(117, 117, 117));
    font-family: 'Source Code Pro', monospace;
}

.main {
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: 10px;
    justify-content: center;
    align-items: center;
}

.header-container {
    display: flex;
    margin-top: -5px;
    margin-bottom: -5px;
    justify-content: center;
    align-items: center;
}

.header {
    font-size: small;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}

.header-container a {
    text-decoration: none;
    color:rgb(0, 0, 0);
    font-weight: 700;
}

.header-container a:hover {
    color:rgb(35, 95, 95);
}

.calculator-grid {
    display: grid;
    margin-top: 2rem;
    grid-template-columns: repeat(4, 6rem);
    grid-template-rows: minmax(7rem, auto) repeat(5, 6rem);
    justify-content: center;
}

.calculator-grid>button {
    cursor: pointer;
    font-size: 2rem;
    border: 1px solid rgba(255, 255, 255, 0.548);
    outline: none;
    background-color: rgba(255, 255, 255, 0.562);
}

.calculator-grid>button:hover,
.calculator-grid>button:focus {
    background-color: rgba(255, 255, 255, 0.719);
}

.span-two {
    grid-column: span 2;
}

.output {
    grid-column: 1 / -1;
    background-color: rgba(0, 0, 0, 0.75);
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    padding: .75rem;
    word-wrap: break-word;
    word-break: break-all;
}

.output .previous-operator {
    color: rgb(255, 255, 255, 0.75);
}

.output .current-operator {
    color: rgb(255, 255, 255);
    font-size: 2.5rem;
}